import React from "react";
import { v4 as uuidv4 } from "uuid";

const NoteContext = React.createContext();

const NoteProvider = ({ children }) => {
  const [notes, setNotes] = React.useState(data);
  const [visible, setVisible] = React.useState(false);
  const [selectedColor, setSelectedColor] = React.useState("#fff");
  const [title, setTitle] = React.useState("");
  const [content, setContent] = React.useState("");

  const toggleModal = () => setVisible(!visible);

  const addNote = () => {

    if (!title || !content) {
      return;
    }
    setNotes([
      ...notes,
      {
        id: uuidv4(),
        title,
        content,
        backgroundColor: selectedColor,
        dateCreated: new Date().toISOString(),
      },
    ]);
    setTitle("");
    setContent("");
    setSelectedColor("#fff");
    toggleModal();
  };

  const deleteNote = (id) => {
    setNotes(notes.filter((note) => note.id !== id));
  };

  // Make getNote async to simulate database/API call
  const getNote = async (id) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        const note = notes.find((note) => note.id === id);
        resolve(note);
      }, 500); // Simulating delay
    });
  };

  const updateNote = (id, title, content, backgroundColor) => {
    if (!title || !content) {
      return;
    }

    setNotes(
      notes.map((note) =>
        note.id === id ? { ...note, title, content, backgroundColor } : note
      )
    );
  };

  return (
    <NoteContext.Provider
      value={{
        notes,
        visible,
        selectedColor,
        title,
        content,
        colors,
        toggleModal,
        setTitle,
        setContent,
        setSelectedColor,
        getNote,
        addNote,
        deleteNote,
        updateNote,
      }}
    >
      {children}
    </NoteContext.Provider>
  );
};
